<template>
    <div class="integration-box"  
        v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <div class="header">
            <div class="header-item padding-right" :class="{headerItemActive:customerHeadTab == 0}" @click="postHeadTapBtn(0)">
                <span>积分充值</span>
                <i :class="{isFirstActive:customerHeadTab == 0}"></i>
            </div>
            <div class="header-item padding-right" :class="{headerItemActive:customerHeadTab == 1}" @click="postHeadTapBtn(1)">
                <span>积分明细</span>
                <i :class="{isFirstActive:customerHeadTab == 1}"></i>
            </div>
        </div>
		<div class="zhanwei"></div>
        <div class="integration-cont" v-show="customerHeadTab == 0">
            <div class="title">
                <p>充值金额:</p>
            </div>
            <div class="integration-item">
                <div class="left clearfix" @click="radioGroupChange(1000)">
                    <p>充1000元</p>
                    <p>兑10000积分, 赠1500积分</p>
                </div>
                <div class="right clearfix" @click="radioGroupChange(3000)">
                    <p>充3000元</p>
                    <p>兑30000积分, 赠5500积分</p>
                </div>
                <div class="left clearfix" @click="radioGroupChange(10000)">
                    <p>充10000元</p>
                    <p>兑100000积分, 赠25000积分</p>
                </div>
            </div>
            <div class="clear"></div>
            <div class="pay-foot" v-show="showWx">
                <!-- <div>
                    <p>长按识别二维码</p>
                </div> -->
                <div class="wx-pay">
                    <div class="wx-pay-img">
                        <i class="wx-icon"></i>
                        <span class="wx-font">微信支付</span>
                    </div>
                    
                    <div class="wx-pay-code">
                        <div id="qrcode" ref="qrcode" v-if="wxCode"></div>
                        <img :src="wxPayCodeEx" alt="" v-else/>
                        <p class="long-time">长按识别二维码</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="integration-detail" v-show="customerHeadTab == 1">
            <div v-for="(item, index) in accountDetailsData" :key="index" class="integration-detail-item">
                <div class="left clearfix">
                    <p>{{item.cmemo}}</p>
                    <p>{{item.cadddate}}</p>
                </div>
                <div class="right clearfix" v-if="item.itradetype == 1">            
                    <p class="item-right-increase">+{{item.ipoints}}</p>
                    <p class="item-right-money">{{item.icurrbalance}}</p>
                </div>
                <div class="right clearfix" v-if="item.itradetype == 2">            
                    <p class="item-right-reduce">{{item.ipoints}}</p>
                    <p class="item-right-money">{{item.icurrbalance}}</p>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import { Notify} from 'vant';
import Util from '@/js/mUtils';
import navTab from "@/components/layout/tab.vue";
import {DLDict,DLArea,DLPType,DLIndustry} from '@/js/dl-code';
import recruitingListBack from "@/assets/post/recruiting_list_back.png";
import resumePicOne from "@/assets/qds-img/boy_1.png";
import { MessageBox } from 'mint-ui';
import InfiniteLoading from 'vue-infinite-loading';                     //分页加载组件
import QRCode from 'qrcodejs2'          //二维码
import wxPayCodeEx from "@/assets/wx_pay_code.png"            //没有二维码的图片
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            customerHeadTab: 0,
            recruitingListBack: recruitingListBack, //没有数据
            resumePicOne: resumePicOne,
            active: '3',
            listData:[
                {
                    handle: '充值',
                    handleTime: "2019-03-25  10:20",
                    revenu: 1500,
                    residual: 1500
                },
                {
                    handle: '充值',
                    handleTime: "2019-03-25  10:20",
                    revenu: 1500,
                    residual: 1500
                },
                {
                    handle: '充值',
                    handleTime: "2019-03-25  10:20",
                    revenu: 1500,
                    residual: 1500
                },
            ],
            wxPayCodeEx: '',
            wxCode: '',         //二维码
            capplyid: '',       //流水号
            responaseIstatus: '',       //定时器跑接口返回的状态码，是3或者4则停止定时器，展示对应的消息，置空二维码和选择的充值项
            responaseMsg: '',
            accountDetailsData: [],         //充值明细列表
            qdsPointBalance: 0,            //积分
            loading: false,
            showWx: false,
           
        }
    },
    components: {
        InfiniteLoading,        //分页
        QRCode,
        Notify
    },
    created() {
    },
    mounted() {
        window.scrollTo(0, 0);
        this.accountDetails();                //获取积分
    },
    destroyed(){
        clearInterval(this.getRechargeStatus)
    },
    methods: {
        qrcode () {
            let qrcode = new QRCode('qrcode', {  
                width: 100,  // 设置宽度 
                height: 100, // 设置高度
                text: this.wxCode
            })  
        },
        postHeadTapBtn(index) {
            this.customerHeadTab = index
            if(this.customerHeadTab == 0) {
                this.customerHeadTab = 0;
            }else if(this.customerHeadTab == 1) {
                this.customerHeadTab = 1;
            }
            
        },
        accountDetails() {          //明细
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.axios(that.qdsGlobalApiUrl + 'getflowPoint?token=' + token, {          //获取积分流水
                method: 'post',
                timeout: 10000
            })
            .then((response) => {
                if(response.data.code == '200') {
                    response.data.entity.forEach((item, index) => {
                        item.cadddate = item.cadddate.split('.')[0]
                    })
                    that.accountDetailsData = response.data.entity;
                }else {
                    Notify({
                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            })
            .catch((error) => {

            })
        },
        radioGroupChange(rechangeValue) {                //充值接口
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.wxCode = '';
            that.loading = true
            that.showWx = true
            that.axios(that.qdsGlobalApiUrl + 'getWeChatQRCode?token=' + token, {
                method: 'post',
                params: {
                    imoney: rechangeValue
                },
            })
            .then((response) => {
                if(response.data.code == '200') {
                    that.loading = false
                    that.wxPayCodeEx = '';
                    that.wxCode = response.data.entity.url;                     //二维码
                    that.capplyid = response.data.entity.capplyid;              //流水号
                    that.$nextTick (function () {
                        that.qrcode();
                    })
                    if(that.capplyid && that.wxCode) {
                        that.getRechargeStatus = setInterval(() => {            //定时器跑，判断用户是否充值，充值的状态
                            that.axios(that.qdsGlobalApiUrl + 'getPayIstatus?token=' + token, {
                                method: 'post',
                                params: {
                                    capplyid: that.capplyid
                                },
                                timeout: 10000
                            })
                            .then((response) => {
                                if(response.data.code == '200') {
                                    if(response.data.entity) {
                                        that.responaseIstatus = response.data.entity.istatus;               //istatus状态：0已申请1已发送2已确定 3已加款 4错误
                                        that.responaseMsg = response.data.entity.cerrdesc;
                                        if(that.responaseIstatus == 3) {                    //状态：0已申请1已发送2已确定 3已加款 4错误
                                            clearInterval(that.getRechargeStatus);
                                            that.wxCode = '';
                                            that.capplyid = '';
                                            that.rechangeValue = '';
                                            that.getUserBasicData();                //充值成功获取积分，关闭弹窗
                                            that.detailsPopupModal = false;
                                            that.rechangePopup = false;
                                            // that.wxPayCodeEx = wxPayCodeEx     //充值默认图
                                            Notify({
                                                message: '充值成功',
                                                duration: 2000,
                                                background: '#20D3B3'
                                            })
                                        }else if(that.responaseIstatus == 4) {
                                            clearInterval(that.getRechargeStatus);
                                            that.wxCode = '';
                                            that.capplyid = '';
                                            that.rechangeValue = '';
                                            that.getUserBasicData();                //充值失败获取积分，关闭弹窗
                                            that.detailsPopupModal = false;
                                            that.rechangePopup = false;
                                            // that.wxPayCodeEx = wxPayCodeEx     //充值默认图
                                            Notify({
                                                message: that.responaseMsg,
                                                duration: 2000,
                                                background: '#FF6262'
                                            })
                                        }
                                    }else {
                                        clearInterval(that.getRechargeStatus)
                                    }
                                }else {
                                    clearInterval(that.getRechargeStatus)
                                    that.wxCode = '';
                                    that.capplyid = '';
                                    that.rechangeValue = '';
                                    that.getUserBasicData();                //充值失败获取积分，关闭弹窗
                                    that.detailsPopupModal = false;
                                    that.rechangePopup = false;
                                    // that.wxPayCodeEx = wxPayCodeEx     //充值默认图
                                    Notify({
                                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                                        duration: 2000,
                                        background: '#FF6262'
                                    })
                                }
                            })
                            .catch((error) => {

                            })
                        },2000)
                    }else {
                        that.wxCode = '';
                        that.capplyid = '';
                        that.rechangeValue = '';
                        that.getUserBasicData();                //充值失败获取积分，关闭弹窗
                        that.detailsPopupModal = false;
                        that.rechangePopup = false;
                        // that.wxPayCodeEx = wxPayCodeEx     //充值默认图
                        Notify({
                             message: '没有订单号或者二维码',
                            duration: 2000,
                            background: '#FF6262'
                        })
                    }
                }else if(response.data.code == '500'){
                    that.loading = false
                    that.wxCode = '';
                    Notify({
                        message: '二维码正在积极抢修，若有疑问请联系客服哦 021-22876396~' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }else {
                    that.loading = false
                    that.wxCode = ''
                    Notify({
                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            })
            .catch((error) => {
                that.loading = false
            })
        },
    }
}
</script>

<style scoped>
.clear{
    clear: both;
}
.integration-box{
    background-color: #fff;
}
.integration-box .zhanwei{
	height: 2.4375rem;
	width: 100%;
}
.integration-box .header{
    display: -webkit-flex;
    display: flex;
    justify-content: space-around;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 2.4375rem;
    line-height: 2.4375rem;
    background-color: #fff;
    border-bottom: 1px solid #E5E5E5;
}
.integration-box .header .header-item{
    position: relative;
    /* width: 33%; */
    height: 100%;
    font-size: 1rem;
    color: #0B231F;
    text-align: center;
    cursor: pointer;
    /* font-weight: bold; */
}
.integration-box .header .right-red-slot{
    position: absolute;
    top: .6875rem;
    right: .0625rem;
    display: inline-block;
    width: 1.1rem;
    height: 1.1rem;
    line-height:  1.1rem;
    -webkit-transform: translateY(-50%) translateX(100%);
    transform: translateY(-50%) translateX(100%);
    background-color: #FB5C55;
    border-radius: 50%;
    color: #fff;
    font-size: .5625rem;
    text-align: center;
    border: 1px solid #fff;
}
.integration-box .header .headerItemActive{
    color: #0B231F;
}
.integration-box .header-item .isActive{
    position: absolute;
    left: 1.0625rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #5FBCA8;
}
.integration-box .header-item .isFirstActive{
    position: absolute;
    left: .875rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #5FBCA8;
}
.integration-cont {
    margin: 0 .9375rem;
    background-color: #fff;
}
.integration-cont .title p {
    line-height: 3.125rem;
    font-size: .875rem;
    font-weight: bold;
    color: #0B231F;
}
.integration-cont .integration-item{
    float: left;
}
.integration-cont .integration-item .left {
    float: left;
    height: 3.75rem;
    width: 10.5rem;
    background: #F5F5F5;
    text-align: center;
    margin-bottom: .625rem;
    color: #0B231F;
}
.integration-cont .integration-item .left:hover {
    float: left;
    height: 3.75rem;
    width: 10.5rem;
    background: #FF963D;
    text-align: center;
    margin-bottom: .625rem;
    color: #fff;
}
.integration-cont .integration-item .right {
    float: right;
    height: 3.75rem;
    width: 10.5rem;
    background: #F5F5F5;
    text-align: center;
    margin-bottom: .625rem;
    color: #0B231F;
}
.integration-cont .integration-item .right:hover {
    float: right;
    height: 3.75rem;
    width: 10.5rem;
    background: #FF963D;
    text-align: center;
    margin-bottom: .625rem;
    color: #fff;
}
.integration-cont .integration-item .left p:nth-child(1) {
    font-size: 18px;
    line-height: 2.3rem;
    font-weight: bold;
}
.integration-cont .integration-item .left p:nth-child(2) {
    font-size: 12px;
}
.integration-cont .integration-item .right p:nth-child(1) {
    font-size: 18px;
    line-height: 2.3rem;
    font-weight: bold;
}
.integration-cont .integration-item .right p:nth-child(2) {
    font-size: 12px;
}

/* 明細 */
.integration-box .integration-detail{
    margin: 0 .9375rem;
}
.integration-box .integration-detail .integration-detail-item{
    border-bottom: 1px solid #E5E5E5;
    width: 100%;
    height: 3.375rem;
}
.integration-box .integration-detail .left{
    float: left;
    padding: .625rem 0;
}
.integration-box .integration-detail .left p:nth-child(1){
    font-size: .875rem;
    color: #0B231F;
}
.integration-box .integration-detail .left p:nth-child(2){
    font-size: .625rem;
    color: #818D8D;
}
.integration-box .integration-detail .right{
    float: right;
    padding: .625rem 0;
}
.integration-box .integration-detail .right p:nth-child(1){
    font-size: .875rem;
    color: #FB5C55;
}
.integration-box .integration-detail .right p:nth-child(2){
    font-size: .75rem;
    color: #0B231F;
}
.infinite-load{
    display: inline-block;
    margin-top: 1rem;
}

.pay-foot{
    /* padding: 80px 0 0 180px; */
    /* float: left; */
}
.pay-foot .wx-pay{
    /* height: 100px;
    line-height: 100px; */
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top: 3.125rem;
    padding-bottom: 3.125rem;
}
.long-time {
    font-size: .875rem;
    color: #666;
}
.wx-pay .wx-pay-img{
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-between; */
}
.wx-pay .wx-icon{
    display: inline-block;
    width: 4.375rem;
    height: 4.375rem;
    background: url(../../assets/wx_icon.png);
}
.wx-pay .wx-font{
    font-size: .875rem;
    color: #666;
}
.wx-pay .wx-pay-code{
    display: inline-block;
    /* width: 6.25rem;
    height: 6.25rem; */
    padding: 5px;
}
.wx-pay-code img{
    display: inline-block;
    width: 100%;
    height: 100%;
}
.wx-pay-code div{
    display: inline-block;
    width: 100%;
    height: 100%;
}
.wx-pay-code div img{
    display: inline-block;
    width: 100%;
    height: 100%;
}
</style>





