<template>
    <div class="personal-page">
        <div>
            <div v-show="!showIntegration">
                 <header class="post-head">
                    <a href="javascript:;" class="back" @click="backHistory"></a> 
                    <span class="head-font">个人中心</span>
                </header>
                <!-- <mt-header title="个人中心" fixed class="">
                    <router-link to="/" slot="left">
                        <mt-button icon="back"></mt-button>
                    </router-link>
                </mt-header> -->
                <div class="zhanwei"></div>
                <div class="box">
                    <img :src="customService" alt="" class="service-img" @click="errorsBtn">
                    <div class="title">
                        <div class="img-name" @click="seeBasicInfor">
                            <img :src="resumePicOne" alt="">
                            <div class="name-sex">
                                <p class="name">{{userName}}</p>
                                <p class="sex">
                                    <span v-if="sex">{{sex}}</span>
                                    <span v-if="position">/{{position}}</span>
                                </p>
                            </div>
                        </div>
                        <div class="integration" @click="integrationClick">
                            <span class="num">{{integration}}</span>
                            <div>
                                <img :src="personalStar" alt="">
                                <span class="star">积分</span>
                            </div>
                        </div>
                    </div>
                    <div class="company-name" @click="seeCompanyInfor">
                        <p>{{companyName}}</p>
                        <p>{{certificationStatus}}</p>
                    </div>
                </div>
                <!-- 没有认证成功状态下是遮罩层 -->
                <div class="no-attestation" v-if="certificationStatus != '认证成功'">
                    <p class="tips-text" v-if="certificationStatus == '未认证'">请先认证企业，查看数据统计</p>
                    <p class="tips-text" v-if="certificationStatus == '认证中'">企业认证进行中，请不要重复认证</p>
                    <p class="tips-text" v-if="certificationStatus == '认证失败'">企业认证失败</p>
                    <div class="no-attestation-item">
                        <p @click="goAttestation" class="state-text" v-if="certificationStatus == '未认证'">认证企业</p>
                        <p @click="goAttestation" class="state-text" v-if="certificationStatus == '认证中'">认证中</p>
                        <p @click="failReason" class="fail-reason" v-if="certificationStatus == '认证失败'">失败原因</p>
                        <p @click="goAttestation" class="state-text" v-if="certificationStatus == '认证失败'">认证失败</p>
                    </div>
                </div>
                <div class="data-statistics">
                    <div class="title">
                        <p>数据统计</p>
                    </div>
                    <div class="statistics-section">
                        <div class="statistics-item" v-for="(item,index) in statisticsList" :key="index" @click="getStatistics(item.path)">
                            <p class="num"><span>{{item.number}}</span><span>个</span></p>
                            <p>{{item.name}}</p>
                        </div>
                    </div>
                </div>
                <div class="data-statistics">
                    <div class="title">
                        <p>更多服务</p>
                    </div>
                    <div class="statistics-section" v-if="certificationStatus == '认证成功'">
                        <div class="statistics-item" v-for="(item,index) in serveList" :key="index" @click="skip(item.path)">
                            <img :src="item.icon" alt="">
                            <p>{{item.name}}</p>
                        </div>
                    </div>
                    <div :class="certificationStatus == '认证成功'?'statistics-section':'statistics-section statistics-section-other'" v-else>
                        <div class="statistics-item" @click="skip(serveList[1].path)">
                            <img :src="serveList[1].icon" alt="">
                            <p>{{serveList[1].name}}</p>
                        </div>
                        <div class="statistics-item" @click="skip(serveList[3].path)">
                            <img :src="serveList[3].icon" alt="">
                            <p>{{serveList[3].name}}</p>
                        </div>
                    </div>
                </div>
                <div class="out-login">
                    <span @click="goLogin">退出登录</span>
                </div>
            </div>
            <integration v-show="showIntegration"></integration>
            <div class="left_trapezoid" v-show="!showIntegration"></div>
            <div class="right_trapezoid" v-show="!showIntegration"></div>
            <nav-tab :fatherActive='active' :fatherIntegration="showIntegration" @repeatData='showIntegration = false'></nav-tab>
            <van-popup v-model="ShowFailReason" class="remark-fail">{{certificationRemark}}</van-popup>
        </div>
        <div class="errors" v-show="selfLoading">
            <div class="errors-item">
                <p class="phone-num">021-22876396</p>
                <div class="errors-item-btn">
                    <p @click="cancelLoading">取消</p>
                    <p @click="cancelLoading"><a href="tel:021-22876396">呼叫</a></p>
                </div>
            </div>
        </div>
    </div>
  
</template>
<script>
import Util from '@/js/mUtils';
import { Notify} from 'vant';
import integration from "@/views/personal/integration"; // 积分页面
import navTab from "@/components/layout/tab.vue";
import resumePicOne from "@/assets/qds-img/boy_1.png";
import personalStar from "@/assets/qds-img/personal-star.png";
import loginPhone from "@/assets/qds-img/login-phone.png";
import personalSet from "@/assets/qds-img/personal_set.png";
import consultSet from "@/assets/qds-img/consult_set.png";
import pwdSet from "@/assets/qds-img/personal_pwd_set.png"
import integrationIcon from "@/assets/qds-img/integration_icon.png"
import customService from "@/assets/qds-img/custom_service.png"
export default {
    data() {
        return{
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            active: '4',
            userName:'',
            sex: '',
            position: '',
            integration: '',   // 积分
            companyName: '',
            certificationStatus: '',   // 认证状态
            newCustomerListCount: 0,  // 合作客户数
            newPositionListCount: 0,  // 合作客户数
            resumePicOne: resumePicOne,
            personalStar: personalStar,
            pwdSet: pwdSet,
            consultSet: consultSet,
            loginPhone: loginPhone,
            personalSet: personalSet,
            integrationIcon: integrationIcon,
            customService: customService,
            qdsUserId: '',
            showIntegration: false,
            statisticsList: [
                {
                    name:'在招职位',
                    number: 0,
                    path:'/qdsPostList'
                },
                {
                    name:'合作客户',
                    number: 0,
                    path:'/qdsCustomerManager'
                },
                {
                    name:'我的简历',
                    number: 0,
                    path:"/qdsSearchResume?selected=" + 1
                },
                {
                    name:'投递记录',
                    number: 0,
                    path:"/qdsSearchResume?selected=" + 2
                },
            ],
            serveList: [
                 {
                    name:'换绑手机号',
                    icon: personalSet,
                    path: '/qdsChangePhone'
                },
                {
                    name:'咨询设置',
                    icon: consultSet,
                    path: '/qdsAdvisorySetup'
                },
                {
                    name:'修改密码',
                    icon: pwdSet,
                    path: '/qdsChangePassword'
                },
                {
                    name:'积分充值',
                    icon: integrationIcon,
                    path: '/qdsPersonal'
                },
            ],
            searchParams: {  //列表加载参数
                jobTypeCode1:'',
                jobTypeCode2:'',
                jobTypeCode3:'',
                resumeName:'',
                resumeId:'',
                corpId:'',
            },
            certificationRemark: '', // 失败原因
            ShowFailReason: false, // 失败原因
            selfLoading: false,
            invoiceInfoModelCompanyName: '',            //发票名称，用来判断有没有填写发票、跳转积分或者发票填写页面

        }
    },
    mounted() {
        this.qdsUserId = localStorage.getItem('qdsSCorpUserId')
        this.certificationStatus = sessionStorage.getItem('qdsMCertificationStatus')
        if(this.certificationStatus == 1) {
            this.certificationStatus = '未认证'
        }else if(this.certificationStatus == 2) {
            this.certificationStatus = '认证中'
        }else if(this.certificationStatus == 3) {
            this.certificationStatus = '认证成功'
        }else if(this.certificationStatus == 4) {
            this.certificationStatus = '认证失败'
        }
        this.getUserBasicData()
        this.authUserBasicData()
        this.getNewCustomerList()
        this.getNewPositionList()
        this.getNewResumeList()
        this.dataList()
        this.getChannelUserInvoice();           //获取发票信息

    },
    components: {
        navTab,
        integration,
    },
    beforeDestroy () {
        clearInterval(this.failSetTimeout)
    },
    methods: {
         backHistory() {
            window.history.back(-1)
        },
        cancelLoading() {
            this.selfLoading = false;
        },
        errorsBtn() {
            this.selfLoading = true;
        },
        seeCompanyInfor() {
            if(this.certificationStatus == '认证成功') {
                this.$router.push('/qdsCompanyInfor')
            }else {
                return false
            }
        },
        seeBasicInfor() {
            this.$router.push('/qdsBasicInfor')
        },
        getStatistics(path) {
            if(path == '/qdsPersonal') {
                this.showIntegration = true;
            }else {
                this.$router.push(path)
            } 
        },
        skip(path) {
            if(path == '/qdsPersonal') {
                if(this.invoiceInfoModelCompanyName == '') {
                    Notify({
                        message: '为了您的账户安全，请填写完发票信息再进行充值哦~',
                        duration: 2000,
                        background: '#E6A23C'
                    })
                    this.failSetTimeout = setTimeout(() => {
                        this.$router.push('/qdsBasicInfor')
                    },2000)
                }else {
                    this.showIntegration = true;
                }
            }else {
                this.$router.push(path)
            } 
        },
        failReason() {
            this.ShowFailReason = true
        },
        goLogin() {
             if(location.href.indexOf("m.qh.uat.youlanw.com") == -1) {
                document.cookie = "qdsToken" + "=" + '' + ";path=/"
                sessionStorage.removeItem("certificationStatus")  
                this.authAState = ""    
                this.$router.push('/')
                sessionStorage.setItem('customUserStatus','未显示') 
            }else {
                document.cookie = "qdsToken" + "=" + '' + ";domain=m.qh.uat.youlanw.com;path=/";
                sessionStorage.removeItem("certificationStatus")   
                this.authAState = ""   
                this.$router.push('/')
                sessionStorage.setItem('customUserStatus','未显示')   
            }
             
        },
        authUserBasicData() {       //认证中、认证成功、认证失败请求的接口，展示的右侧渠道商的企业名称
            var that = this;
            var token = Util.getCookie('qdsMToken');
            if(that.certificationStatus == '认证中' || that.certificationStatus == '认证成功' || that.certificationStatus == '认证失败') {
                that.axios(that.qdsGlobalApiUrl + 'ylw/qhChannelCorp/api/getChannelInfoByUserId?token=' + token,{
                    method: 'get'
                })
                .then( response=> {
                    if(response.data.code == 10000) {
                        if(response.data.data.corpInfo){
                            localStorage.setItem('corpId',response.data.data.corpInfo.id)           //咨询电话设置页面需要使用
                            that.companyName = response.data.data.corpInfo.corpName
                        }
                    }else {
                        Notify({
                            message: '获取渠道商企业名称失败',
                            duration: 2000,
                            background: '#FF6262'
                        })
                    }
                })
                .catch(error => {
                })
                }
        },
        getUserBasicData() {        //获取右侧用户基础信息
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'getChannelUser?token=' + token,{
                method: 'post',
                timeout: 10000
            })
            .then( response=> {
                if(response.data.code == "200") {
                    that.userName = response.data.entity.userName
                    that.position = response.data.entity.position
                    if(that.certificationStatus == '未认证') {
                        that.companyName = response.data.entity.clueName
                    }
                    if(response.data.entity.certificationRemark) {          //渠道商认证失败获取的失败原因
                        that.certificationRemark = response.data.entity.certificationRemark
                    }
                    if(response.data.entity.gender == 0) {
                        that.sex = ''
                    }else if(response.data.entity.gender == 1) {
                        that.sex = '男'
                    }else if(response.data.entity.gender == 2) {
                        that.sex = '女'
                    }
                    if(response.data.entity.pointBalance){
                        that.integration = response.data.entity.pointBalance.split(".")[0];
                    }else {
                        that.integration = 0;
                    }
                       
                }else {
                    Notify({
                        message: '获取用户基础数据失败',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            })
            .catch(error => {

            })
        },
        getNewCustomerList() {                  //首页合作客户数
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'ylw/qhChannelCorp/api/homeData?token=' + token, {
                method: 'get',
                params: { 
                    page: 1,       // 用户id
                    limit: 4,
                },
                timeout: 10000
            })
            .then((response)=>{
                if(response.data.code == 10000) {
                    that.statisticsList[1].number = response.data.data.customerCount;
                }else {
                    Notify({
                        message: '合作客户数据加载异常，请联系客服021-22876396',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            }).catch((error)=>{
            })
        },
        getNewPositionList() {          //首页在招职位 数
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/selectDemandInfo?token=' + token, {
                method: 'post',
                timeout: 10000
            })
            .then((response)=>{
                if(response.data.code == 10000) {
                    that.statisticsList[0].number = response.data.data.count;
                }else {
                    Notify({
                        message: '在招职位数据加载异常，请联系客服021-22876396',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            }).catch((error)=>{
            })
        },
        getNewResumeList() {                //首页简历部分(最新投递简历、个人简历库数量)
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'channelResume/homeApplyAndResumeInfo?token=' + token, {
                method: 'get',
                params: { 
                    userId: that.qdsUserId,       // 用户id
                    size: 4,
                },
                timeout: 10000
            })
            .then((response)=>{
                if(response.data.code == 200) {
                    that.statisticsList[2].number = response.data.data.resumeRepositoryCount;
                }else {
                    Notify({
                        message: '最新投递简历数据加载异常，请联系客服021-22876396',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            }).catch((error)=>{
            })
        },
        dataList() {       // 投递记录 数量
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl +"channelApply/applyList?token=" + token, 
                {
                    method: "get",
                    params: {
                        userId:that.qdsUserId,
                        searchParams:that.searchParams,   // 搜索条件
                        page: 1,               // 页码
                        size: 10,               // 一页的数据量
                    },
                    timeout:10000
                }
                ).then(function(response) {
                    if(response.data.code == 200) {
                        var dataArr = response.data.data.applys
                        if(response.data.data.applys.length > 0){
                            that.statisticsList[3].number = response.data.data.applys.length
                        }
                    }
                        
                })
                .catch(function(error) {
                        
                });
        },
        getChannelUserInvoice() {               //获取发票信息
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.axios(that.qdsGlobalApiUrl + 'getChannelUserInvoice?token=' + token, {
                methods: 'post',
                timeout: 10000
            })
            .then((response) => {
                if(response.data.code == "200") {
                    if(response.data.entity.companyName) {
                        that.invoiceInfoModelCompanyName = response.data.entity.companyName
                    }
                }else {
                    Notify({
                        message: '获取发票信息失败' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    })
                }
            })
            .catch((error) => {

            })
        },
        integrationClick() {
            if(this.invoiceInfoModelCompanyName == '') {
                Notify({
                    message: '为了您的账户安全，请填写完发票信息再进行充值哦~',
                    duration: 2000,
                    background: '#E6A23C'
                })
                this.failSetTimeout = setTimeout(() => {
                    this.$router.push('/qdsBasicInfor')
                },2000)
            }else {
                this.showIntegration = true;
            }
        },
        goAttestation() {
            this.$router.push('/qdsAttestation')
        },
        onClickLeft() {
            Toast('返回');
        },
    }
}
</script>

