        justify-content: space-between;
    }
    .personal-page .data-statistics .statistics-section-other {
        height: 4.25rem;
        padding: .625rem .9375rem;
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
    }
    .personal-page .data-statistics .statistics-section .statistics-item {
        text-align: center;
    }
    .personal-page .data-statistics .statistics-section .statistics-item img{
        height: 1.0625rem;
    }
    .personal-page .data-statistics .statistics-section .statistics-item p:nth-child(2){
        font-size: .75rem;
        color: #818D8D;
        line-height: 1.875rem;
    }
    .personal-page .data-statistics .statistics-section .statistics-item .num span:nth-child(1){
        font-size: 1rem;
        color: #FB5C55;
        font-weight: 600;
    }
    .personal-page .data-statistics .statistics-section .statistics-item .num span:nth-child(2){
        font-size: .625rem;
        color: #818D8D;
    }
    .personal-page .out-login {
        margin-left: .9375rem;
        margin-right: .9375rem;
        margin-top: 3.1875rem;
    }
    .personal-page .out-login span {
        width: 20rem;
        height: 2.75rem;
        line-height: 2.75rem;
        display: inline-block;
        margin: 0 .8125rem;
        border: 1px solid #2DA1A4;
        border-radius: 1.375rem;
        color: #2DA1A4;
        font-size: 1.125rem;
        text-align: center;
        letter-spacing: .1em;
    }
    .personal-page .no-attestation {
        position: absolute;
        top: 11.375rem;
        left: 0;
        margin-left: .9375rem;
        margin-right: .9375rem;
        height: 7.6875rem;
        width: 21.5625rem;
        text-align: center;
        background-color: rgba(0, 0, 0, .5);
    }
    .personal-page .no-attestation .tips-text {
        padding-top: 1.5rem;
        padding-bottom: .5rem;
        font-size: .9375rem;
        color: #FFFFFF;
    }
    .personal-page .no-attestation .no-attestation-item {
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
    }
    .personal-page .no-attestation .no-attestation-item .fail-reason {
        display: inline-block;
        height: 1.875rem;
        line-height: 1.875rem;
        width: 7.75rem;
        text-align: center;
        font-size: .875rem;
        color: #FFFFFF;
        border: 1px solid #ffffff;
        border-radius: .9375rem;
        margin: .75rem 0;
        
    }
    .personal-page .no-attestation .no-attestation-item .state-text {
        display: inline-block;
        height: 1.875rem;
        line-height: 1.875rem;
        width: 7.75rem;
        text-align: center;
        font-size: .875rem;
        color: #FFFFFF;
        background: #FF963D;
        border-radius: .9375rem;
        margin: .75rem 0;
        
    }
    .remark-fail {
        position: fixed;
        top: 50%;
        left: 50%;
        padding-top: 10px;
        text-indent: .5em;
        min-width: 67%;
        min-height: 30%;
        max-height: 100%;
        overflow-y: auto;
        background-color: #fff;
        -webkit-transition: .3s ease-out;
        transition: .3s ease-out;
        -webkit-overflow-scrolling: touch;
        -webkit-transform: translate3d(-50%,-50%,0);
        transform: translate3d(-50%,-50%,0);
    }
    /* 呼叫电话 */
     .errors{
        position: fixed;
        top: 0;
        z-index: 99;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, .6)
    }
    .errors .errors-item {
        position: absolute;
        top: 40%;
        left: 22%;
        width: 13.4375rem;
        /* height: 7.0625rem; */
        background: #fff;
        border-radius: .5rem;
    }
    .errors .errors-item p{
        text-align: center;
    }
    .errors .errors-item .phone-num{
        font-size: .875rem;
        color: #0B231F;
        line-height: 3.3125rem;
        
    }
    .errors .errors-item .errors-item-btn {
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    .errors .errors-item .errors-item-btn p:nth-child(1){
        font-size: 1rem;
        color: #818D8D;
        background: #E5E5E5;
        line-height: 2.3rem;
        width: 50%;
        border-bottom-left-radius: .5rem;
    }
    .errors .errors-item .errors-item-btn p:nth-child(2){
        font-size: 1rem;
        color: #fff;
        background: #2DA1A4;
        line-height: 2.3rem;
        width: 50%;
        border-bottom-right-radius: .5rem;
    }
    .errors .errors-item .errors-item-btn p:nth-child(2) a{
        display: inline-block;
        text-decoration: none;
        font-size: 1rem;
        color: #fff;
        background: #2DA1A4;
        line-height: 2.3rem;
        width: 100%;
        border-bottom-right-radius: .5rem;
    }
</style>




